import { useGuardContract, useDidContract } from '../../hooks/useContract'
import { useSingleCallResult } from '../multicall/hooks'
import { Contract } from '@ethersproject/contracts'

export function useGetDidAddress(): string | undefined {
    const guardContract = useGuardContract()
    const res = useSingleCallResult(guardContract, 'did')
    if (res.result && !res.loading) {
        return res.result[0]
    }
    return undefined
}

export function useNftBalance(didAddress: string | undefined, account: string | null | undefined): number | undefined {
    const didContract = useDidContract(didAddress)
    const res = useSingleCallResult(didContract, 'balanceOf', [account ?? ""])
    if (res.result && !res.loading) {
        return parseInt(res.result[0])
    }
    return undefined
}


export function useApplyLength(didContract: Contract | null): number {
    const res = useSingleCallResult(didContract, 'applyLength')
    if (res.result && !res.loading) {
        return parseInt(res.result[0])
    }
    return 0
}

export function useApplyForList(didContract: Contract | null, start: number, end: number): Array<any> {
    const res = useSingleCallResult(didContract, 'applyList', [start, end])
    if (res.result && !res.loading) {
        var list = Array<any>()
        console.log('res.result[0]', res.result[0])
        for (var i = 0; i < res.result[0].length; i++) {
            var content = res.result[0][i]
            list[i] = {
                id: content[0],
                owner: content[1],
                state: content[2],
                applyTime: content[3] ? getTime(content[3]) : "-",
                identity: content[4],
                region: content[5],
                email: content[6],
                approveTime: content[7] ? getTime(content[7]) : "-",
                approvedBy: content[8],
                extra: content[9],
            }
        }
        console.log('res.list', list)

        return list
    }
    return []
}

function getTime(timestamp: any) {
    var day = "",
        month = "",
        hours = "",
        minutes = "",
        seconds = "",
        d = new Date(Number(timestamp.toString()) * 1000)
    day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate().toString()
    month = d.getMonth() + 1 < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1).toString()
    hours = d.getHours() < 10 ? '0' + d.getHours() : d.getHours().toString()
    minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes().toString()
    seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds().toString()
    var date = (d.getFullYear() + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds);
    return date
}





